import { Component, OnInit, Input } from '@angular/core';
import { Movie } from '../../models/movie';
import {ActivatedRoute} from '@angular/router'; //laf vien router dang sing cletan (ton tai duy nhat trong app)
import {Location} from '@angular/common'; //go back tro lai man hinh
import {MovieService} from '../movie.service';

@Component({
  selector: 'app-movie-detail',
  templateUrl: './movie-detail.component.html',
  styleUrls: ['./movie-detail.component.css']
})

export class MovieDetailComponent implements OnInit {




  @Input() movie: Movie;
  constructor(
    private route: ActivatedRoute,
    private movieService: MovieService,
    private location: Location){}
    // 3 injeantable: co the trich xuat o bat ki dau co cung gia tri va co the thay doi


  ngOnInit() {
      this.getMovieFromRoute();
  }
  getMovieFromRoute(): void{
    const id = +this.route.snapshot.paramMap.get('id');
    console.log(`this.route.snapshot.paramMap = ${JSON.stringify(this.route.snapshot.paramMap)}`);
    //Call service to "get movie from id" ?
    this.movieService.getMovieFromId(id).subscribe(movie => this.movie = movie); 
    //subscribe quan sat xem khi nao co ket qua, co ket qua thi chui va ham ben trong
    // dau + de bien thanh so

  }


  save(): void{
    this.movieService.updateMovie(this.movie).subscribe(()=>this.goBack());
  }
  goBack (): void{
    this.location.back();
    //quay tro lai man hinh truoc
  }

}



