import { Component, OnInit } from '@angular/core';
import {Movie} from '../../models/movie'; //vi co mang movie len phai import movie
import {MovieService} from '../movie.service';//service lam nhiem vu lay du lieu

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
  movies: Movie[] = [];
  constructor( private movieService: MovieService) { }
  getMovies(): void{
      this.movieService.getMovies().subscribe(movies=>this.movies = movies.slice(0,10));
  //subscribe THeo doi, quan sat khi naof ham co ket qua, co ket qua thi se chui vao. slice(1,5) lay tu 1 - 5
    }
  ngOnInit() {
    this.getMovies();
    //goi ham trong  ngOnInit chuw ko goi trong constructor se a/h den performer
  }

}



